import React, {useEffect} from 'react';
import './style.css'

//  interface: í˜ì´ì§€ë„¤ì´ì…˜ ì»´í¬ë„ŒíŠ¸ properties //
interface Props {
    pagination: {
        page: number;            // 0-based index
        size: number;
        totalPages: number;
        totalElements: number;
        first: boolean;
        last: boolean;
        numberOfElements: number;
    };
    onPageChange: (page: number) => void; // pageëŠ” 1 ê¸°ë°˜ìœ¼ë¡œ ì „ë‹¬
}

//  component: í˜ì´ì§€ë„¤ì´ì…˜ ì»´í¬ë„ŒíŠ¸ - ì „ìì •ë¶€ í”„ë ˆì„ì›¤ ê¸°ì¤€  //
const Paging = React.memo(({ pagination, onPageChange }: Props) => {
    const { page, totalPages } = pagination;

    if (totalPages <= 1) return null;

    const currentPage = page + 1;  // UI í‘œì‹œìš©
    const lastPage = totalPages;

    const pages = [];
    // í•­ìƒ í¬í•¨: ì²« í˜ì´ì§€
    pages.push(1);

    // í˜„ì¬ í˜ì´ì§€ -2 ë¶€í„° +2ê¹Œì§€
    const start = Math.max(2, currentPage - 2);
    const end = Math.min(lastPage - 1, currentPage + 2);

    if (start > 2) pages.push('...');
    for (let i = start; i <= end; i++) pages.push(i);
    if (end < lastPage - 1) pages.push('...');

    // í•­ìƒ í¬í•¨: ë§ˆì§€ë§‰ í˜ì´ì§€
    if (lastPage > 1) pages.push(lastPage);


    //  render: í˜ì´ì§€ë„¤ì´ì…˜ ì»´í¬ë„ŒíŠ¸ ë Œë”ë§ //
    return (
        <div className="pagination-container">
            <button
                className="page-btn"
                disabled={currentPage === 1}
                onClick={() => onPageChange(currentPage - 1)}
            >
                ã€ˆ
            </button>

            {pages.map((p, idx) =>
                typeof p === 'number' ? (
                    <button key={idx} className={`page-btn ${p === currentPage ? 'active' : ''}`} onClick={() => onPageChange(p)}>
                        {p}
                    </button>
                ) : (
                    <span key={idx} className="ellipsis">...</span>
                )
            )}

            <button
                className="page-btn"
                disabled={currentPage === lastPage}
                onClick={() => onPageChange(currentPage)}
            >
                ã€‰
            </button>
        </div>
    )
});

export default Paging;